.page-header {
    width: 1440px;
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    padding-left: 14px;
    padding-right: 10px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 33px;

    &__logo {
      display: flex;
    }

    &__logo svg {
      width: 52px;
      height: 52px;
    }

    &__logo-wrapper {
      margin-left: 16px;
      margin-top: 5px;
    }

    &__logo-name {
      font-family: "Roboto Condensed", "Tahoma", sans-serif;
      font-weight: 700;
      font-size: 25px;
      line-height: 29px;
      color: #FFFFFF;
      margin: 0;
      margin-bottom: 1px;
    }

    &__logo-description {
      font-family: "Roboto", "Tahoma", sans-serif;
      font-style: normal;
      font-weight: 500;
      font-size: 12px;
      line-height: 14px;
      color: #788891;
      margin: 0;
    }

    &__menu {
      list-style: none;
      padding: 0;
      margin: 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-wrap: wrap;
      width: 663px;
      margin-top: 2px;
    }

    &__menu-item a {
      text-decoration: none;
      color: #FFFFFF;
      display: flex;
      align-items: center;

      &:hover {
        color: #00b2ff;
      }

      &:active {
        color: #788891;
      }
    }

    &__menu-item svg {
      display: block;
      margin-right: 18px;
      fill: #00b2ff;
      cursor: pointer;
    }

    &__menu-item--mail svg {
      width: 19px;
      height: 14px;
    }

    &__menu-item--phone svg {
      width: 18px;
      height: 18px;
    }

    &__menu-item--request {
      margin-left: -35px;
      margin-right: 5px;
    }

    &__menu-item--request button {
      width: 180px;
      min-height: 45px;

      padding-top: 10px;
      padding-bottom: 10px;

      border: 1px solid #FFFFFF;
      box-sizing: border-box;
      border-radius: 5px;

      background: transparent;
      font-family: "Roboto Condensed", "Tahoma", sans-serif;
      font-weight: 700;
      font-size: 14px;
      line-height: 16px;
      text-align: center;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      color: #FFFFFF;
      cursor: pointer;

      &:hover {
        color: #00b2ff;
        border: 1px solid #00b2ff;
      }

      &:active {
        color: #788891;
        border: 1px solid #788891;
        outline: none;
      }
    }
  }

@media (max-width: $desktop-width) {
  .page-header {
    width: 100%;
  }
}


@media (max-width: $tablet-width) {
  .page-header {
    width: 100%;
    padding-left: 24px;
    padding-right: 17px;
    padding-top: 18px;

    &__logo {
      margin-top: 2px;
    }

    &__menu {
      width: 91px;
      margin-top: 0;
    }

    &__menu-item span {
      @include visually-hidden()
    }

    &__menu-item svg {
      margin-right: 0;

      &:hover {
        fill: #FFFFFF;
      }

      &:active {
        opacity: 0.5;
      }
    }

    &__menu-item--mail svg {
      width: 25.33px;
      height: 18.67px;
    }

    &__menu-item--phone svg {
      width: 24px;
      height: 24px;
    }

    &__menu-item--request button {
      display: none;
    }
  }
}


@media (max-width: $mobile-width) {
  .page-header {
    width: 100%;
    padding-left: 17px;
    padding-right: 18px;
    padding-top: 15px;

    &__logo svg {
      width: 38px;
      height: 38px;
    }

    &__logo-wrapper {
      display: none;
    }

    &__menu {
      width: 71px;
      margin-top: 0;
    }
  }
}
