body {
  font-family: "PT Mono", "Tahoma", sans-serif;
  font-weight: 400;
  color: #666666;
  min-width: 320px;
  margin: 0;
  height: 100%;
  display: flex;
  flex-direction: column; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.6); }

.overlay--closed {
  display: none; }

.menu-closed ul {
  visibility: hidden;
  opacity: 0;
  position: absolute;
  top: 100%;
  left: 15px;
  transform: translateY(-2em);
  z-index: -1;
  transition: all 0.3s ease-in-out 0s, visibility 0s linear 0.3s, z-index 0s linear 0.01s; }

.menu-opened {
  /*pointer-events:none*/ }

.menu-opened ul {
  visibility: visible;
  opacity: 1;
  position: static;
  z-index: 5;
  transition: all 0.3s ease-in-out 0s, visibility 0s linear 0.1s, z-index 0s linear 0.01s; }

/*.menu-opened a {
  pointer-events: auto;
}*/
.modal-wrapper {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
  z-index: 100; }

.modal-wrapper--closed {
  display: none; }

